:root {
    --body-background-color: #222222;
    --body-text-color: #fefefe;
    --body-link-color: #ffad00;

    --info-box-background-color: white;
    --info-box-text-color: black;
    --info-box-i-color: blue;

    --nav-bar-color: #303030;
  
    --nav-link-background-color: #353535;
    --nav-link-background-hover-color: #404040;
    --nav-link-text-hover-color: #ff8d00;

    --nav-blinking-name-text-color: orange;

    --blog-post-page-content-h2-color: orange;
    --blog-post-page-content-h3-color: #fec069;
    --blog-post-page-content-h4-color: #ffa6ce;
    --blog-post-page-content-bold-color: #a6ff6b;
    --blog-post-page-content-code-background-color: #272822;
    --blog-post-page-content-code-text-color: var(--blog-post-page-content-h4-color);

    --blockquote-background-color: #efefef;
    --blockquote-text-color: black;
}

body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.body {
    background: var(--body-background-color);
    color: var(--body-text-color);
    font-family: Lato, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"
}

.body a {
    color: var(--body-link-color);
    text-decoration: none;
}

.body a:hover {
    color: var(--body-text-color);
}

/* Small screens */
@media screen and (max-width: 700px) {
    .content {
        width: 100%;
    }

    .wide-content {
        width: 100%;
    }
}

/* Big screens */
@media screen and (min-width: 700px) {
    .content {
        width: 600px;
    }

    .wide-content {
        width: 100%;
    }
}

.centered-content {
    text-align: center;
    margin: auto;
    padding: 10px;
}

.construction {
    border-radius: 4px;
    width: 560px;
    margin-top: 4px;
    margin-bottom: 8px;
}

.work-in-progress {
    text-decoration: line-through;
    color: gray;
}

.whitespace {
    white-space: pre;
}

.lou-bricant {
    font-size: 52px;
    line-height: 0.7;
    margin-top: 20px;
    margin-bottom: 20px;
}

.flip-horizontally * {
    transform: scaleX(-1);
}

.force-right-align {
    color: green !important;
    text-align: right !important;
}

.vertically-centered {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-align: center;
    justify-content: center;
}

.float-left {
    float: left;
}

.align-right {
    text-align: right;
}

/* --------- */

.nav-bar-wrapper {
    background: var(--nav-bar-color);
    padding-top: 0px;
    margin-top: 0px;
    padding-bottom: 2px;
    line-height: 0.5;
}

.nav-bar {
    text-align: center;
}

.nav-link-wrapper {
    text-decoration: none;
}

.nav-bar .nav-link-wrapper:not(:last-child) {
    margin-right: 8px;
}

.nav-link-button-selected > input {
    border-bottom: 1px solid var(--nav-link-text-hover-color);
}

.nav-link-button {
    color: var(--body-text-color);
    background: var(--nav-link-background-color);

    border-radius: 4px;
    border: 0;

    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 2px;
    min-width: 100px;
    min-height: 40px;

    transition: color 0.1s linear, background 0.133s linear;
    font-family: monospace;
}

.nav-link-button::-moz-focus-inner {
    border: 0;
}

.nav-link-button:hover {
    cursor: pointer;
    color: var(--nav-link-text-hover-color);
    background: var(--nav-link-background-hover-color);
}

.nav-bar-icon {
    border-radius: 50%;
    width: 56px;
    margin-right: 12px;
    margin-bottom: 8px;
    margin-top: 8px;
}

.nav-blinking-name {
    color: var(--nav-blinking-name-text-color);

    border-radius: 4px;
    border: 0;

    min-width: 100px;
    padding-bottom: 4px;
    margin-left: 12px;

    font-family: monospace;
    font-size: 20px;
}

.nav-blinking-name::-moz-focus-inner {
    border: 0;
}

.blinking {
  animation: blinking-keyframes 1.3s linear infinite;
}

@keyframes blinking-keyframes {
  0%  { opacity: 1; }
  10% { opacity: 0; }
  50% { opacity: 0; }
  60% { opacity: 1; }
}

.info-box {
    background: var(--info-box-background-color);
    color: var(--info-box-text-color);
    border-radius: 4px;
    /* max-width: 500px; */
    /* min-width: 100px; */
    padding-right: 32px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-left: auto;
    margin-right: 20px;
}

.info-box-content-column p:first-child {
    margin-top: 0px;
}

.info-box-content-column p:last-child {
    margin-bottom: 0px;
}

.info-box-i-column {
    vertical-align: top;
}

.info-box-i {
    color: var(--info-box-i-color);
    margin: 16px;
}

.blog-post-preview {
    margin-bottom: 40px;
}

.blog-post-preview-content {
    text-align: justify;
}

.blog-post-page-credits {
    display: grid;
    grid-template-columns: 65px 1fr;
    grid-gap: 5px;

    width: 90%;
    margin: auto;
    margin-top: 12px;
    margin-bottom: 32px;
    padding: 7px;

    --border: 1px solid #676767;
    border-top: var(--border);
    border-bottom: var(--border);
}

.blog-post-page-credits-icon {
    border-radius: 50%;
    width: 40px;
    margin-top: 4px;
    margin-bottom: 4px;
    margin-left: 0px;
}

.blog-post-page-content p {
    text-align: justify;
}

.blog-post-page-content strong {
    color: var(--blog-post-page-content-bold-color)
}

.blog-post-page-content img {
    border-radius: 4px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.blog-post-page-content li {
    text-align: left;
}

.blog-post-page-content h2 {
    color: var(--blog-post-page-content-h2-color);
    border-top: 1px solid var(--blog-post-page-content-h2-color);
    border-bottom: 1px solid var(--blog-post-page-content-h2-color);
    text-align: center;
    margin-top: 40px;
}

.blog-post-page-content h3 {
    color: var(--blog-post-page-content-h3-color);
    text-align: left;
}

.blog-post-page-content h4 {
    color: var(--blog-post-page-content-h4-color);
    text-align: left;
}

.blog-post-page-content img {
    max-width: 100%;
}

.blog-post-page-content blockquote {
    color: var(--blockquote-text-color);
    background: var(--blockquote-background-color);
    border-left: 10px solid #ccc;
    border-radius: 5px;
    margin: 1.5em 10px;
    padding: .5em 10px;
    quotes: "\201C""\201D""\2018""\2019";
}

.blog-post-page-content blockquote:before {
    content: open-quote;
}

.blog-post-page-content blockquote:after {
    content: close-quote;
}

.blog-post-page-content blockquote>p {
    display: inline;
}

.blog-post-page-content code {
    color: var(--blog-post-page-content-code-text-color);
    font-size: 14px;
}

.blog-post-page-content pre {
    font-size: 14px !important;
}